</template>
<script>
    import Clipboard from 'clipboard'
    import {WOW} from 'wowjs';
    let provider, signer, defaultAccount, networkVersion;
    let usdtContract,masContract,duiContract,nftCaoContract,nftContract;
    let bnbUrl = 'https://bscscan.com/address/';
    export default {
        data() {
            return {
                lang:'zh',
                lang_list:{},
                lang_show:false,
                address: '',
                short_address: 'connect',
                parents:'',
                url:'',
                disable: false,
                show: false,
                meanShow: false,
                useAble:false,
                timeShow:false,
                nftShow:false,
                nftUrl:'',
                usdtBanner:0,
                masBanner:0,
                nftBanner:0,
                contract:'',
                info:{
                    lock: '',
                    nftlock: '',
                    rate: 0,
                    round: 0,
                    stage: 0,
                    stageText: 0,
                    startTime: 0,
                    totalSupply: 0,
                    superior: "",
                    total: "",
                    nftNo:'',
                    nftId:0,
                    dhlock:'',
                    day:0,
                    hour:0,
                    minute:0,
                    second:0,
                },
                activeUsdt:0,
                masaNum:'',
                masNum:'',
                masBuy:'',
                carddisable:false,
                ftpdisable:false,
                nftProfit:false,
            }
        },
        created: function () {
            this.contract = this.api.masaAddress
        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang')?sessionStorage.getItem('lang'):'en';
            this.lang_list = this.en.lang;
            switch(this.lang){
                case 'zh':
                    this.lang_list = this.zh.lang;
                    break;
                case 'en':
                    this.lang_list = this.en.lang;
                    break;
                case 'hw':
                    this.lang_list = this.hw.lang;
                    break;
            }
            this.parents = this.$route.query.p_address?this.$route.query.p_address:this.getUrlParam('p_address');
            this.checkNet();
            var wow = new WOW(
                {
                    boxClass: 'wow',      // animated element css class (default is wow)
                    animateClass: 'animated', // animation css class (default is animated)
                    offset: 0,          // distance to the element when triggering the animation (default is 0)
                    mobile: true,       // trigger animations on mobile devices (default is true)
                    live: true,       // act on asynchronously loaded content (default is true)
                    callback: function (box) {
                        // the callback is fired every time an animation is started
                        // the argument that is passed in is the DOM node being animated
                    },
                    scrollContainer: null // optional scroll container selector, otherwise use window
                }
            );
            wow.init();

        },
        methods: {
            switchLang(type,event){
                $('.zu_land_button_active').removeClass('zu_land_button_active');
                let toggle = event.currentTarget;
                $(toggle).addClass('zu_land_button_active');
                switch(type){
                    case 'zh':
                        sessionStorage.setItem('lang','zh');
                        break;
                    case 'en':
                        sessionStorage.setItem('lang','en');
                        break;
                    case 'hw':
                        sessionStorage.setItem('lang','hw');
                        break;
                }
                location.reload();
            },
            showLand(){
              this.lang_show = true;
            },
            toContract(){
                window.open(bnbUrl+this.contract);
            },
            async buyNft(event){
                var _this = this;
                if(!_this.info.nftlock){
                    _this.useAble = true;
                    return false;
                }
                if(_this.nftProfit){
                    return false;
                }
                _this.nftProfit = true;
                var toggle = event.currentTarget;
                $(toggle).css('background-color','#dddddd');
                const gasPrice = await provider.getGasPrice();
                try{
                    let parentAddress =_this.parents ;
                    if(!parentAddress){
                        parentAddress = '0x0000000000000000000000000000000000000000'
                    }
                    let res = await nftCaoContract.BuyNFT(parentAddress,{
                        gasPrice: gasPrice,
                        gasLimit:400000,
                        value:ethers.utils.parseEther('1.28')
                    })
                    if(res){
                        _this.$toast(_this.lang_list[42]);
                        _this.nftProfit = false;
                        setTimeout(function () {
                            location.reload();
                        },3000)

                    }
                }catch (e) {
                    _this.nftProfit = false;
                    _this.$toast(_this.lang_list[43]);
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            noShow(){
                this.$toast(this.lang_list[44]);
            },
            showNft(){
                this.nftShow = true;
            },
            async submitBuy(event){
                let _this = this;
                let toggle = event.currentTarget;
                const gasPrice = await provider.getGasPrice()
                let timestamp =parseInt(Date.parse(new Date())/1000);
                if(_this.info.startTime>0 && _this.info.startTime>timestamp){
                    _this.timeShow = true;
                    let shenTime = parseInt(_this.info.startTime)-parseInt(timestamp);
                    let yuTime =setInterval(function () {
                        _this.formatSeconds(shenTime);
                        shenTime--;
                    },1000);
                    return false;
                }
                if(!_this.info.lock){
                    _this.useAble = true;
                    return false;
                }
                if(this.activeUsdt>this.usdtBanner){
                    _this.$toast(_this.lang_list[45]);
                    return false;
                }
                if(this.activeUsdt<=0){
                     _this.$toast(_this.lang_list[46]);
                    return false;
                }
                if(this.ftpdisable){
                    return false;
                }
                this.ftpdisable = true;
                $(toggle).css('background','#dddddd');
                let etherNum = ethers.utils.parseEther (this.activeUsdt.toString());
                if(!_this.parents){
                    _this.parents = '0x0000000000000000000000000000000000000000';
                }
                await usdtContract.allowance(defaultAccount,_this.api.duiAddress).then(async res =>{
                    try{
                        if(parseFloat(ethers.utils.formatEther(res))>0){
                            let res1 = await duiContract.PreSale(_this.parents,etherNum,{
                                gasPrice: gasPrice,
                                gasLimit:400000,
                            });
                            if(res1){
                                _this.ftpdisable = false;
                                _this.$toast(_this.lang_list[47]);
                                setTimeout(function () {
                                    location.reload();
                                },3000)
                            }
                        }else{
                            let tx = await usdtContract.approve(_this.api.duiAddress,'0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff',{
                                gasPrice: gasPrice,
                                gasLimit:250000
                            });
                            if(tx){
                                let res1 = await duiContract.PreSale(_this.parents,etherNum,{
                                    gasPrice: gasPrice,
                                    gasLimit:400000,
                                });
                                if(res1){
                                    _this.ftpdisable = false;
                                    _this.$toast(_this.lang_list[47]);
                                    setTimeout(function () {
                                        location.reload();
                                    },3000)
                                }
                            }
                        }
                    }catch (e) {
                        _this.ftpdisable = false;
                        _this.$toast(_this.lang_list[48]);
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }).catch(err => {
                    _this.ftpdisable = false;
                    _this.$toast(_this.lang_list[48]);
                    return false;
                });
            },
            formatSeconds(value) {
                var theTime = parseInt(value);// 需要转换的时间秒
                var theTime1 = 0;// 分
                var theTime2 = 0;// 小时
                var theTime3 = 0;// 天
                if(theTime<=0){
                    this.info.day = '00';
                    this.info.hour = '00';
                    this.info.minute = '00';
                    this.info.second = '00';
                }else{
                    if(theTime > 60) {
                        theTime1 = parseInt(theTime/60);
                        theTime = parseInt(theTime%60);
                        if(theTime1 > 60) {
                            theTime2 = parseInt(theTime1/60);
                            theTime1 = parseInt(theTime1%60);
                            if(theTime2 > 24){
                                //大于24小时
                                theTime3 = parseInt(theTime2/24);
                                theTime2 = parseInt(theTime2%24);
                            }
                        }
                    }
                    this.info.day = parseInt(theTime3)<10?'0'+theTime3:theTime3;
                    this.info.hour = parseInt(theTime2)<10?'0'+theTime2:theTime2;
                    this.info.minute = parseInt(theTime1)<10?'0'+theTime1:theTime1;
                    this.info.second = parseInt(theTime)<10?'0'+theTime:theTime;
                }

            },
            checkMasBuy(){
                if(this.masBuy>0){
                    this.activeUsdt = this.masBuy*0.02;
                }else{
                    this.activeUsdt = 0;
                }
            },
            //兑换
            async submitExchange(event){
                let _this = this;
                const gasPrice = await provider.getGasPrice();
                if(!this.info.dhlock){
                    this.useAble = true;
                    return false;
                }
                if(this.masNum>this.masBanner){
                    _this.$toast(_this.lang_list[49]);
                    return false;
                }
                if(_this.masNum<500){
                    _this.$toast(_this.lang_list[50]);
                    return false;
                }
                let etherNum = ethers.utils.parseEther (_this.masNum.toString());
                if(this.carddisable){
                    return false;
                }
                this.carddisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                await masContract.allowance(defaultAccount,_this.api.duiAddress).then(async res =>{
                    try{
                        if(parseFloat(ethers.utils.formatEther(res))>0){
                            let res1 = await duiContract.DH(etherNum,{
                                gasPrice: gasPrice,
                                gasLimit:400000,
                            });
                            if(res1){
                                _this.carddisable = false;
                                _this.$toast(_this.lang_list[51]);
                                setTimeout(function () {
                                    location.reload();
                                },3000)
                            }
                        }else{
                            let tx = await masContract.approve(_this.api.duiAddress,'0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff',{
                                gasPrice: gasPrice,
                                gasLimit:250000
                            });
                            if(tx){
                                let res1 = await duiContract.DH(etherNum,{
                                    gasPrice: gasPrice,
                                    gasLimit:400000,
                                });
                                if(res1){
                                    _this.carddisable = false;
                                    _this.$toast(_this.lang_list[51]);
                                    setTimeout(function () {
                                        location.reload();
                                    },3000)
                                }
                            }
                        }
                    }catch (e) {
                        _this.carddisable = false;
                        _this.$toast(_this.lang_list[52]);
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }).catch(err => {
                    _this.carddisable = false;
                    _this.$toast(_this.lang_list[52]);
                    return false;
                });
            },
            checkDui(){
                if(this.masNum>=500){
                    this.masNum = parseInt(parseInt(this.masNum)/500)*500
                    this.masaNum =  parseInt(this.masNum/10000);
                }
            },
            checkNum(){
                let num = this.masNum;
                this.masaNum = num/10000;
            },
            hiddleShow(){
                this.nftShow = false;
            },
            hiddleTimeShow(){
                this.timeShow = false;
            },
            hiddleLangShow(){
                this.lang_show = false;
            },
            hiddleKaiShow(){
                this.useAble = false;
            },
            copyLink(){
                var _this = this;
                let clipboard = new Clipboard('#tag', {
                    text: function () {
                        return   _this.url
                    }
                });
                clipboard.on('success', e => {
                    _this.$toast(_this.lang_list[53]);
                    // 释放内存
                    clipboard.destroy()
                });
                clipboard.on('error', e => {
                    _this.$toast(_this.lang_list[54]);
                    clipboard.destroy()
                })
            },
            //显示菜单
            showMean(){
                this.meanShow = true;
            },
            //关闭菜单
            closeMean(){
                this.meanShow = false;
            },
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;
                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin();
                } catch (e) {
                    _this.$toast(_this.lang_list[55]);
                }
            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                window.location.reload();
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(async function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list[55]);
                            return false;
                        }
                        else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list[55]);
                            return false;
                        }
                        else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list[55]);
                            return false;
                        }
                        else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list[55]);
                            return false;
                        }
                        else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        }
                        else {
                            _this.$toast(_this.lang_list[55]);
                            return false;
                        }
                        usdtContract = new ethers.Contract(_this.api.usdtAddress, usdtAbi, signer);
                        masContract = new ethers.Contract(_this.api.masAddress, masAbi, signer);
                        duiContract = new ethers.Contract(_this.api.duiAddress, duiAbi, signer);
                        nftCaoContract = new ethers.Contract(_this.api.nftCaoAddress, nftCaoAbi, signer);
                        nftContract = new ethers.Contract(_this.api.nftAddress, nftAbi, signer);
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                            _this.url = config.baseUrl+'/#/home/?p_address='+defaultAccount;
                            //获取MAS余额
                            _this.getBanner();
                            //查询兑换，公募,NFT 是否开启
                            _this.getInfo(_this.address);

                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
            async getInfo(address){
                let _this = this;
                await duiContract.getIdByAddr(address).then(res => {
                    let id = ethers.utils.formatEther(res)*1000000000000000000;
                    // id=1;
                    if(id>0){
                        duiContract._playerMap(id).then(result => {
                            if(result.superior!='0x0000000000000000000000000000000000000000'){
                                _this.info.superior = result.superior;
                                _this.parents = result.superior;
                            }
                            _this.info.total = ethers.utils.formatEther(result.total);
                           console.log(result)
                        }).catch(err => {
                            console.log(err);
                            return;
                        });
                    }
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await duiContract._dhLOCK().then(res => {   //兑换开关
                    _this.info.dhlock = res;
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await duiContract._LOCK().then(res => {   //预售开关
                    _this.info.lock = res;
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await duiContract.startTime().then(res => {   //预售时间
                    let endTime= ethers.utils.formatEther(res)*1000000000000000000;
                    if(endTime!=100000000000){
                        _this.info.startTime = endTime;
                    }
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await duiContract.round().then(res => {   //预售轮数
                    _this.info.round =11-parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await duiContract.stage().then(res => {   //剩余数量
                    _this.info.stage =  Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                    _this.info.stageText = _this.info.stage.toLocaleString('en-us');
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await nftCaoContract._NFTLOCK().then(res => {   //NFT开关
                    _this.info.nftlock = res;
                }).catch(err => {
                    console.log(err);
                    return;
                });
                if(_this.nftBanner>0){
                    await nftCaoContract.getIdByAddr(address).then(res => {   //NFT开关
                        let id  = parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                        if(id<1){
                            return false;
                        }
                        _this.info.nftId =id;
                        _this.nftUrl = require('../../assets/images/nft/card'+(_this.info.nftId%6+1)+'.png');
                        if(id<10){
                            _this.info.nftNo = '#MAS000'+id;
                        }else if(id<100){
                            _this.info.nftNo = '#MAS00'+id;
                        }else{
                            _this.info.nftNo = '#MAS0'+id;
                        }
                    }).catch(err => {
                        console.log(err);
                        return;
                    });
                }

            },
            async getBanner() {  //获取余额
                let _this = this;
                await usdtContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.usdtBanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await masContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.masBanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await nftContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.nftBanner =parseInt(ethers.utils.formatEther(res)*1000000000000000000);
                }).catch(err => {
                    console.log(err);
                    return;
                });
                await nftContract.totalSupply().then(res => {   //剩余数量
                    let totalSupply = parseInt(ethers.utils.formatEther(res));
                    _this.info.totalSupply = totalSupply;
                    _this.info.rate =parseFloat(parseInt(totalSupply/3000*100)/100);
                    $('#jie_img').css('left',"calc("+_this.info.rate+'% - 9px)');
                    $('#jie_left').css('width',_this.info.rate+'%');
                    $('#jie_right').css('width',(100-_this.info.rate)+'%');
                }).catch(err => {
                    console.log(err);
                    return;
                });

            },
        }
    }
</script>


